"use client";

import React, { useEffect, useRef  } from "react";
import { gsap } from "gsap";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

export default function LoginPage() {
  const cardRef = useRef<HTMLDivElement>(null);
  const inputsRef = useRef<HTMLDivElement[]>([]);

  useEffect(() => {
    // Animate card on mount
    gsap.fromTo(
      cardRef.current,
      { y: 50, opacity: 0 },
      { y: 0, opacity: 1, duration: 0.8, ease: "power3.out" }
    );

    // Animate input fields one by one
    gsap.fromTo(
      inputsRef.current,
      { opacity: 0, y: 20 },
      {
        opacity: 1,
        y: 0,
        duration: 0.6,
        ease: "power2.out",
        stagger: 0.15,
        delay: 0.5,
      }
    );
  }, []);

  return (
    <div className="min-h-screen flex items-center justify-center bg-[#f5f5f5] p-4">
      <div
        ref={cardRef}
        className="w-full max-w-md p-8 bg-[#ffffff] shadow-lg rounded-lg sm:p-10"
      >
        <h1 className="text-3xl font-bold text-center mb-8 text-gray-900 tracking-tight">
          Login
        </h1>
        <form className="space-y-6">
          <div
            className="space-y-2"
            ref={el => { if (el) inputsRef.current[0] = el; }}
          >
            <Label
              htmlFor="username"
              className="block text-sm font-medium text-gray-700 tracking-wide"
            >
              Username
            </Label>
            <Input
              id="username"
              type="text"
              placeholder="Enter your username"
              className="mt-1 block w-full rounded-md border-gray-300 focus:border-[#0068ff] focus:ring-[#0068ff] transition-colors duration-200"
            />
          </div>
          <div
            className="space-y-2"
            ref={el => { if (el) inputsRef.current[1] = el; }}
          >
            <Label
              htmlFor="password"
              className="block text-sm font-medium text-gray-700 tracking-wide"
            >
              Password
            </Label>
            <Input
              id="password"
              type="password"
              placeholder="Enter your password"
              className="mt-1 block w-full rounded-md border-gray-300 focus:border-[#0068ff] focus:ring-[#0068ff] transition-colors duration-200"
            />
          </div>
          <div ref={el => { if (el) inputsRef.current[2] = el; }}>
            <Button
              type="submit"
              className="w-full bg-[#0068ff] hover:bg-blue-700 text-white font-semibold py-3 rounded-md transition-colors duration-200 tracking-wide"
            >
              Login
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}
